import AsyncModal from "./AsyncModal.js";

class Prompt extends AsyncModal {
    constructor(inputType = "text") {
        super();
        const field = document.createElement("input");
        field.type = inputType;
        this.field = field;
        this.dialog.appendChild(field);
        this.okButton = document.createElement("button");
        this.okButton.appendChild(document.createTextNode("OK"));
        this.dialog.appendChild(this.okButton);

        this.cancelButton = document.createElement("button");
        this.cancelButton.appendChild(document.createTextNode("Cancel"));
        this.dialog.appendChild(this.cancelButton);

        this.okButton.addEventListener("click", () => { this.accept(); });
        this.cancelButton.addEventListener("click", () => { this.reject(); });
    }
    getResult() {
        return this.field.value;
    }
}
export default Prompt;


